import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import Iframe from 'react-iframe'


function News() {

    const  latestNews = [
        {
            desc:`Prof KSMS Raghava Rao has been awarded the prestigious "Life Time Achievement Award" by International Association of Engineering and Food.`,
        },
        {
            desc:`Mr. Allabakshi S. M, Civil Engineering Research scholar received the first prize in "Best oral presentation award category".`,
        },
        {
            desc:`Mr. V. Sunil - MS Scholar under guidance of Dr. Uday Kumar Sukumar & Dr. K. Anki Reddy, Department of Chemical Engineering, got selected for "Khorana Program of Internships - 2023"`,
        },
        {
            desc: `Mr. Vikrant Yadav and Mr. Sonu Jana - Department of Physics, Research scholars have been awarded the prestigious "MITACS GLOBALINK RESEARCH" Award.`,
        },
        {
            desc:`Mr. Allabakshi S. M, Civil Engineering Research scholar received the first prize in "Best Paper Award Category".`,
        },
        {
            desc:`Ms. Vajitha. G, Civil Engineering Research scholar received the "Best poster presentation award".`,
        }

        
    ];

    const  latestAnnounce = [
        {
            desc:`Advertisement for the position of Junior Research Fellow`,
        },
        {
            desc:`Notification for Technical Positions - Engineering Unit (Outsourced Position)`,
        },
        {
            desc:`Notification for Sports Coaches & Gym Trainer - Hostel and Mess Establishment (Outsourced Position)`,
        },
        {
            desc: `Orientation Program for MS/Ph.D. Research Scholars - July 24, 2023`,
        },{
            desc: `Orientation Program for M.Tech/M.Sc./MPP - July 27, 2023`,
        },{
            desc: `Advertisement for the position of Junior Research Fellow - Mechanical Engineering`,
        },{
            desc: `Notification for Laboratory Assistant (Outsourced Position) - Department of Physics`,
        }
        
    ];

  return (
    <>
        <section className="event event03 pt-120 pb-90 p-relative fix" style={{ background: "#f7f9ff" }}>
            <div className="container">
                <div className="row">
                
                <div className="col-lg-6 col-md-12  wow fadeInUp animated" data-animation="fadeInUp" data-delay=".4s" >
                    <div className="section-title center-align mb-50 wow fadeInDown animated" data-animation="fadeInDown" data-delay=".4s" >
                        <h5>
                            <i className="fal fa-graduation-cap" /> News
                        </h5>
                        <h2>Latest News</h2>
                    </div>
                    <div className='newspaper scrollbar2'>
                        {latestNews.map((currNews, index) => (
                            <div className="event-item mb-10 hover-zoomin">
                                <div className="event-content">
                                    <div className="icon">
                                        <i className="fal fa-newspaper" />
                                    </div>
                                    <div className="text ">
                                        <h6>
                                            <Link to="/">
                                            {" "}
                                            {currNews.desc}
                                            </Link>
                                        </h6>
                                    </div>
                                </div>
                            </div>
                        ))}
                        
                    </div>
                    <div className="slider-btn mt-3 float-end">
                        <Link to="/about" className="btn ss-btn smoth-scroll">
                            View All<i className="fal fa-long-arrow-right" />
                        </Link>
                    </div>
                </div>
                <div className="col-lg-6 col-md-12  wow fadeInUp animated" data-animation="fadeInUp" data-delay=".4s" >
                    <div className="section-title center-align mb-50 wow fadeInDown animated" data-animation="fadeInDown" data-delay=".4s" >
                    <h5>
                        <i className="fal fa-graduation-cap" /> Announcements
                    </h5>
                    <h2>Latest Announcements</h2>
                    </div>
                    <div className='newspaper scrollbar3'>
                        {latestAnnounce.map((currNews, index) => (
                            <div className="event-item mb-10 hover-zoomin">
                                <div className="event-content">
                                    <div className="icon">
                                        <i className="fas fa-bullhorn" />
                                    </div>
                                    <div className="text ">
                                        <h6>
                                            <Link to="/">
                                            {" "}
                                            {currNews.desc}
                                            </Link>
                                        </h6>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                    <div className="slider-btn mt-3 float-end">
                        <Link to="/about" className="btn ss-btn smoth-scroll">
                            View All<i className="fal fa-long-arrow-right" />
                        </Link>
                    </div>
                    </div>
                </div>
            </div>
        </section>

        
    </>
  )
}

export default News