import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import Iframe from 'react-iframe'


function Publications() {

    

    const  latestPublications = [
        {
            title:`Grain boundary- and triple junction-induced martensitic transformations: A phase-field study of effects of grain boundary width and energy`,
            author:`Anup Basak`,
            dated:`09 May 2023`,
            journal: `International Journal of Solids and Structures`,

        },
        {
            title:`A multiphase phase-field study of three-dimensional martensitic twinned microstructures at large strains (Continuum Mechanics and Thermodynamics)`,
            author:`Anup Basak, Valery Levitas`,
            dated:`04 January 2023`,
            journal: ``,

        },
        {
            title:`Computational investigation of NREL Phase-VI rotor: Validation of test sequence-S measurements (Wind Engineering)`,
            author:`Mujahid Shaik, Balaji Subramanian`,
            dated:`27 April 2023`,
            journal: ``,

        },
        {
            title:`Indigenisation of the Quantum Clock: An Indispensable Tool for Modern Technologies`,
            author:`Subhadeep De (IUCAA Pune), Arijit Sharma (IIT Tirupati)`,
            dated:`10 April 2023`,
            journal: ``,

        },

        

        
    ];

  return (
    <>
        <section className="event event03 pt-120 pb-90 p-relative fix" style={{ background: "#f7f9ff" }}>
            <div className="container">
                <div className="row">
                
                    <div className="col-lg-12 col-md-12  wow fadeInUp animated" data-animation="fadeInUp" data-delay=".4s" >
                        <div className="section-title center-align mb-50 wow fadeInDown animated" data-animation="fadeInDown" data-delay=".4s" >
                        <h5>
                            <i className="fal fa-graduation-cap" /> Publications
                        </h5>
                        <h2>Publications</h2>
                        </div>
                        <div className='newspaper publications scrollbar4'>
                            {latestPublications.map((currPubl, index) => (
                                <div className="event-item mb-10 hover-zoomin">
                                    <div className="event-content">
                                        <div className="icon mt-1">
                                            <i class="fal fa-book"></i>
                                        </div>
                                        <div className="text">
                                            <h6>
                                                <Link to="/">
                                                {" "}
                                                {currPubl.title}
                                                </Link>
                                            </h6>
                                            <span className='author'>
                                            <i class="fal fa-user-circle me-1"></i>
                                            {currPubl.author}</span>
                                            
                                            {currPubl.dated &&
                                                <span className='dated'> 
                                                    <i class="fal fa-calendar-alt ms-3 me-1"></i>
                                                {currPubl.dated}</span>
                                            }
                                            {currPubl.journal &&
                                                <span className='journal fst-italic'>
                                                    <i class="fa fa-thumbtack fst-italic ms-3 me-1"></i> {currPubl.journal}</span>
                                            }  
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                        <div className="slider-btn mt-3 float-end">
                            <Link to="/about" className="btn ss-btn smoth-scroll">
                                View All<i className="fal fa-long-arrow-right" />
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </section>

        
    </>
  )
}

export default Publications