import React from 'react'
import { Link } from 'react-router-dom';
import Slider from "react-slick";
import Background from '../../assets/img/slider/IIT_Main_Gate.png'
import Backgroundtwo from '../../assets/img/slider/IIT_Admin_Block.png'
import Marquee from "react-fast-marquee";

function SampleNextArrow(props) {
    const { className, style, onClick } = props;
    return (
        <button type="button" className={ `slick-next ${className}`} style={{ ...style }} onClick={onClick} ><i className="far fa-angle-right"></i></button>
    );
}
  
function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
        <button type="button" className={ `slick-prev ${className}`} style={{ ...style }} onClick={onClick} ><i className="far fa-angle-left"></i></button>
    );
}

function Sliderone() {

    const settings = {
        autoplay: true,
		autoplaySpeed: 10000,
		dots:false ,
		fade: true,
		arrows: true,
		prevArrow: <SamplePrevArrow/>,
		nextArrow: <SampleNextArrow/>,
		responsive: [
			{ breakpoint: 1200, settings: { dots: false, arrows: false } }
		]
      };


  return (
    <>
        <section id="home" className="slider-area fix p-relative">
            {/* <marquee width="100%" direction="left" height="20px" className="marquee glow" behavior="alternate" onmouseover="this.stop();" onmouseout="this.start();">
                <img src="assets/img/icon/new.gif" alt="iconNew" height={15} />
                Information for B.Tech 2023 Batch Students
            </marquee> */}
            <Marquee pauseOnHover={true} speed={100} >
                <img src="assets/img/icon/new.gif" alt="iconNew" height={16} />
                Information for B.Tech 2023 Batch Students
            </Marquee>
            <Slider className="slider-active" style={{ background: "#141b22" }} {...settings}>
                <div>
                    <div className="single-slider slider-bg"  style={{ backgroundImage: `url(${Background})` , backgroundSize: "cover", backgroundAttachment : "fixed" }} >
                    <div className="container">
                        <div className="row">
                        <div className="col-lg-7 col-md-7">
                            <div className="slider-content s-slider-content mt-130">
                            <h5 data-animation="fadeInUp" data-delay=".4s">
                                Welcome To IIT Tirupati
                            </h5>
                            <h2 data-animation="fadeInUp" data-delay=".4s">
                                Education is the best key success in life
                            </h2>
                            <p data-animation="fadeInUp" data-delay=".6s">
                            IIT Tirupati has a high potential to be an Academic and Research Institute of great repute.
                            </p>
                            <div className="slider-btn mt-30">
                                <Link to="/about" className="btn ss-btn mr-15" data-animation="fadeInLeft" data-delay=".4s" >
                                    Discover More <i className="fal fa-long-arrow-right" />
                                </Link>
                                <Link to="/contact" className="btn ss-btn active" data-animation="fadeInLeft" data-delay=".4s" >
                                    Contact Us <i className="fal fa-long-arrow-right" />
                                </Link>
                            </div>
                            </div>
                        </div>
                        <div className="col-lg-5 col-md-5 p-relative"></div>
                        </div>
                    </div>
                    </div>
                </div>
                <div>
                    <div className="single-slider slider-bg" style={{ backgroundImage: `url(${Backgroundtwo})`, backgroundSize: "cover", backgroundAttachment : "fixed" }} >
                    <div className="container">
                        <div className="row">
                        <div className="col-lg-7 col-md-7">
                            <div className="slider-content s-slider-content mt-130">
                            <h5 data-animation="fadeInUp" data-delay=".4s">
                                Welcome To IIT Tirupati
                            </h5>
                            <h2 data-animation="fadeInUp" data-delay=".4s">
                                Education is the best key success in life
                            </h2>
                            <p data-animation="fadeInUp" data-delay=".6s">
                            The National Institutional Ranking Framework (NIRF), approved by the Ministry of Human Resource Development (MHRD) outlines a methodology to rank institutions across the country.
                            </p>
                            <div className="slider-btn mt-30">
                                <Link to="/about" className="btn ss-btn mr-15" data-animation="fadeInLeft" data-delay=".4s" >
                                Discover More <i className="fal fa-long-arrow-right" />
                                </Link>
                                <Link to="/contact" className="btn ss-btn active" data-animation="fadeInLeft" data-delay=".4s" >
                                Contact Us <i className="fal fa-long-arrow-right" />
                                </Link>
                            </div>
                            </div>
                        </div>
                        <div className="col-lg-5 col-md-5 p-relative"></div>
                        </div>
                    </div>
                    </div>
                </div>

            </Slider>
        </section>

        <section className="service-details-two p-relative">
            <div className="container">
                <div className="row">
                <div className="col-lg-4 col-md-12 col-sm-12">
                    <div className="services-box07">
                    <div className="sr-contner">
                        <div className="icon">
                            <img src="assets/img/icon/dc.png" alt="icon01" />
                        </div>
                        <div className="text">
                        <h5>
                            <Link to="/about">IT Infrastructure - Data Center</Link>
                        </h5>
                        <p>
                        Our data center runs a software-defined private cloud that deploys on our own physical infrastructure...
                        </p>
                        <Link to="/about">
                            Read More <i className="fal fa-long-arrow-right" />
                        </Link>
                        </div>
                    </div>
                    </div>
                </div>
                <div className="col-lg-4 col-md-12 col-sm-12">
                    <div className="services-box07 active">
                    <div className="sr-contner">
                        <div className="icon">
                            <img src="assets/img/icon/sve-icon5.png" alt="icon01" />
                        </div>
                        <div className="text">
                        <h5>
                            <Link to="/about">Technology Innovation Hub (TIH)</Link>
                        </h5>
                      
                        <p>
                        IIT Tirupati Navavishkar I-Hub Foundation (IITTNiF) is set up to host the Technology Innovation Hub...
                        </p>
                        <Link to="/about">
                            Read More <i className="fal fa-long-arrow-right" />
                        </Link>
                        </div>
                    </div>
                    </div>
                </div>
                <div className="col-lg-4 col-md-12 col-sm-12">
                    <div className="services-box07">
                    <div className="sr-contner">
                        <div className="icon">
                            <img src="assets/img/icon/oia.png" alt="icon01" />
                        </div>
                        <div className="text">
                        <h5>
                            <Link to="/about">Office of International Affairs</Link>
                        </h5>
                       
                        <p>
                        The International Affairs Office oversees and coordinates all international activities of IIT Tirupati...
                        </p>
                        <Link to="/about">
                            Read More <i className="fal fa-long-arrow-right" />
                        </Link>
                        </div>
                    </div>
                    </div>
                </div>
                </div>
            </div>
        </section>
    </>
  )
}

export default Sliderone