import React from 'react'
import { Link } from 'react-router-dom'

function Event() {
    const events = [
    {
        venue: 'IIT Tirupati',
        title: 'Body Politics: The Philosophy of Autobiography Through Literature',
        description: 'This GIAN course offers intimate readings of a diverse range of global autobiographical literature...',
        link: '/',
        img: 'assets/img/events/body_politics.png',
        dt: '18',
        my: 'July 2023',
        duration: '5 days',
    },
    {
        venue: 'IIT Tirupati',
        title: 'Pre-incubation contest "Technical Business Ideas 2023"',
        description: `The Pre-incubation cell at IIT
        Tirupati invites the
        application from students,
        stating their innovative...`,
        link: '/',
        img: 'assets/img/events/technical_business_ideas.png',
        dt: '26',
        my: 'July 2023',
        duration: '',
    },{
        venue: 'IIT Tirupati',
        title: 'Indo-German Workshop on Data Mathematics and Scientific Computing',
        description: `Data mathematics plays a critical role in many
        areas of modern science, engineering, and
        technology...`,
        link: '/',
        img: 'assets/img/events/indo_german_workshop.png',
        dt: '8',
        my: 'September 2023',
        duration: '5 days',
    },{
        venue: 'IIT Tirupati',
        title: 'Processing and Fabrication of Advanced Materials (29th PFAM 2023)',
        description: `We welcome you to the Twenty-Ninth International Conference 
        on Processing and Fabrication of...`,
        link: '/',
        img: 'assets/img/events/pfam2023.png',
        dt: '6',
        my: 'July 2023',
        duration: '3 days',
    },{
        venue: 'IIT Madras',
        title: '30th National Conference on Communications (NCC 2024)',
        description: 'The National Conference on Communications (NCC) is the flagship conference of India...',
        link: '/',
        img: 'assets/img/events/ncc2024.png',
        dt: '28',
        my: 'Feb 2024',
        duration: '4 days',
    },{
        venue: 'IIT Tirupati',
        title: 'Five-Day Workshop on "Finite Element Methods- Theory and Practice (Ansys)"',
        description: 'This course is designed to the faculty involved in teaching the course on finite element method...',
        link: '/',
        img: 'assets/img/events/ansys.png',
        dt: '19',
        my: 'June 2023',
        duration: '5 days',
    },
    
];

  return (
    <>
        <section className="event pt-80 pb-90 p-relative fix" style={{backgroundColor:'#cbeef9'}}>
            <div className="animations-06">
                <img src="assets/img/bg/an-img-06.png" alt="an-img-01" />
            </div>
            <div className="animations-07">
                <img src="assets/img/bg/an-img-07.png" alt="contact-bg-an-01" />
            </div>
            <div className="animations-08">
                <img src="assets/img/bg/an-img-08.png" alt="contact-bg-an-01" />
            </div>
            <div className="animations-09">
                <img src="assets/img/bg/an-img-09.png" alt="contact-bg-an-01" />
            </div>
            <div className="container">
                <div className="row">
                    <div className="col-lg-12 p-relative">
                        <div className="section-title center-align mb-50 text-center wow fadeInDown animated"
                            data-animation="fadeInDown"
                            data-delay=".4s">
                            <h5>
                                <i className="fal fa-graduation-cap" /> Our Events
                            </h5>
                            <h2>Upcoming Events</h2>
                        </div>
                        
                    </div>
                </div>
                <div className="row">
                {events.map((event, index) => (

                    <div key={index} className="col-lg-4 col-md-6  wow fadeInUp animated" data-animation="fadeInUp" data-delay=".4s" >
                        <div className="event-item mb-30 hover-zoomin">
                            <div className="thumb">
                                <Link to="/single-event">
                                <img src={event.img} alt="contact-bg-an-01" />
                                </Link>
                            </div>
                            <div className="event-content">
                                <div className="date">
                                <strong>{event.dt}</strong> {event.my}
                                </div>
                                <h3 style={{minHeight:"87px"}}>
                                <Link to="/single-event">
                                    {" "}
                                    {event.title}
                                </Link>
                                </h3>
                                <p>
                                {event.description}
                                </p>
                                <div className="time">
                                {event.duration} <i className="fal fa-long-arrow-right" />{" "}
                                <strong>{event.venue}</strong>
                                </div>
                            </div>
                        </div>
                    </div>
                
                ))}
                
                </div>
                <div className="slider-btn float-end">
                    <Link to="/about" className="btn ss-btn smoth-scroll">
                        View All <i className="fal fa-long-arrow-right" />
                    </Link>
                </div>
            </div>
        </section>
    </>
  )
}

export default Event