import { Link } from 'react-router-dom';
import { menuItems } from './jsonMenu';
import MenuItems from './MenuItems';

const Megamenu = () => {
    return (
        
        <nav id="mobile-menu">
            <ul className="menus">
                {menuItems.map((menu, index) => {
                    
                    const depthLevel = 0;
                    return (
                        <MenuItems
                        items={menu}
                        key={index}
                        depthLevel={depthLevel}
                        />
                    );
                })
                }
            </ul>
        </nav>
        
    )
}

export default Megamenu;