import React from 'react'
import { Link } from 'react-router-dom';
import Slider from "react-slick";

function SampleNextArrow(props) {
    const { className, style, onClick } = props;
    return (
        <button type="button" className={ `slick-next ${className}`} style={{ ...style }} onClick={onClick} ><i className="fal fa-angle-right"></i></button>
    );
}
  
function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
    <button type="button" className={ `slick-prev ${className}`} style={{ ...style }} onClick={onClick} ><i className="fal fa-angle-left"></i></button>
      
    );
}

function Awards() {
    const awards = [
        {
            category: 'Achievement',
            title: 'Life Time Achievement Award',
            description: 'Prof KSMS Raghava Rao has been awarded the prestigious Life Time Achievement Award by...',
            link: '/',
            img: 'assets/img/awards/raghavarao.png',
        },
        {
            category: 'Indian Standard',
            title: 'Member to the panel of Revision of Indian Standard (IS)',
            description: 'Dr. B. Janaki Ramaiah co-opted as a member to the panel of Revision of Indian Standard...',
            link: '/',
            img: 'assets/img/awards/JanakiRamaiah.jpg',
        },
        {
            category: 'Research',
            title: 'DAAD Research Ambassador',
            description: 'Dr. Panchatcharam Mariappan is honoured as DAAD Research Ambassador. The tenure...',
            link: '/',
            img: 'assets/img/awards/PanchatcharamNUMA.jpg',
        },
        {
            category: 'Research',
            title: 'Chemical Research Society of India (CRSI)s Bronze Medal',
            description: 'Dr. Gouriprasanna Roy (Associate Professor, Chemistry) was awarded the Chemical...',
            link: '/',
            img: 'assets/img/awards/Gouriprasanna.png',
        },
        {
            category: 'Research',
            title: 'Research Excellence Award',
            description: 'Dr. Shihabudhen M. Maliyekkal received Research Excellence Award under the...',
            link: '/',
            img: 'assets/img/awards/Shihab.png',
        },
       
    ];

    const settings = {
        dots: false,
        infinite: true,
        arrows: true,
        prevArrow: <SamplePrevArrow/>,
        nextArrow: <SampleNextArrow/>,
        speed: 1000,
        slidesToShow: 4,
        slidesToScroll: 1,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 992,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1
                }
            },
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };

    return (
        <>
         {/* style={{background:'#c9e2f1'}} */}
            <section className="courses csec pt-80 pb-80 p-relative fix" style={{backgroundColor : 'lightblue'}}>
                <div id='wave'></div>
                <div className="animations-01">
                    <img src="assets/img/bg/an-award.png" alt="an-img-01" />
                </div>
                <div className="container">
                <div className="row">
                        <div className="col-lg-12 p-relative">
                            <div className="section-title center-align mb-50 wow fadeInDown animated" data-animation="fadeInDown" data-delay=".4s" >
                                <h5>
                                    <i className="fal fa-graduation-cap" /> Awards
                                </h5>
                                <h2>Awards</h2>
                            </div>
                        </div>
                    </div>
                    <Slider className="row class-active" {...settings}>
                        {awards.map((course, index) => (
                            
                            <div key={index} className="col-lg-4 col-md-6 ">
                                
      
    
                                <div className="courses-item mb-30 hover-zoomin">
                                    <div className="thumb fix">
                                        <Link to={course.link} >
                                            <img src={course.img} alt="contact-bg-an-01" />
                                        </Link>
                                    </div>
                                    <div className="courses-content">
                                        <div className="cat">
                                            <i className="fal fa-thin fa-award" /> {course.category}
                                        </div>
                                        <h5 style={{minHeight:"50px"}}>
                                            <Link to={course.link}>{course.title}</Link>
                                        </h5>
                                        <p>
                                            {course.description}
                                        </p>
                                        <Link to={course.link} className="readmore">
                                            Read More <i className="fal fa-long-arrow-right" />
                                        </Link>
                                    </div>
                                    <div className="icon">
                                        <img src="assets/img/icon/trophy.png" alt="img" />
                                    </div>
                                </div>
                                
                            </div>
                        ))}
                    </Slider>
                    <div className="slider-btn mt-10 float-end">
                        <Link to="/about" className="btn ss-btn smoth-scroll">
                            View All <i className="fal fa-long-arrow-right" />
                        </Link>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Awards